'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { signOut } from 'firebase/auth';
import { auth } from '@/firebase/clientApp';
import styles from '@/styles/home.module.css';

interface SidebarNavigationProps {
  isAuthenticated: boolean;
  currentPage?: string;
}

const SidebarNavigation: React.FC<SidebarNavigationProps> = ({
  isAuthenticated,
  currentPage = ''
}) => {
  const [isSidebarCollapsed, setIsSidebarCollapsed] = useState(false);
  const [windowWidth, setWindowWidth] = useState(typeof window !== 'undefined' ? window.innerWidth : 0);
  
  // Set up window resize listener
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const toggleSidebar = () => {
    setIsSidebarCollapsed(!isSidebarCollapsed);
    
    // Toggle body scroll and overlay for mobile
    if (windowWidth < 768) {
      if (isSidebarCollapsed) {
        // Opening sidebar - prevent scrolling
        document.body.classList.add('menu-open');
      } else {
        // Closing sidebar - allow scrolling
        document.body.classList.remove('menu-open');
      }
    }
  };
  
  const handleSignOut = async () => {
    try {
      await signOut(auth);
      // Close sidebar after sign out on mobile
      if (windowWidth < 768) {
        setIsSidebarCollapsed(true);
        document.body.classList.remove('menu-open');
      }
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  // On mobile, don't render the sidebar at all if it's collapsed
  if (windowWidth < 768 && isSidebarCollapsed) {
    return (
      <button 
        className={styles['toggle-button']} 
        onClick={toggleSidebar}
        aria-label="Open menu"
      >
        ‚ò∞
      </button>
    );
  }

  return (
    <>
      {/* Sidebar toggle button - only shown on mobile when sidebar is open */}
      {windowWidth < 768 && (
        <button 
          className={styles['toggle-button']} 
          onClick={toggleSidebar}
          aria-label="Close menu"
        >
          ‚úï
        </button>
      )}
      
      {/* Menu overlay - only visible on mobile when sidebar is open */}
      {windowWidth < 768 && (
        <div 
          className={`${styles['menu-overlay']} ${!isSidebarCollapsed && windowWidth < 768 ? styles.active : ''}`}
          onClick={toggleSidebar}
          aria-hidden="true"
        />
      )}
      
      {/* Sidebar */}
      <aside className={`${styles['left-sidebar']} ${isSidebarCollapsed && windowWidth >= 768 ? styles.collapsed : ''}`}>
        <div className={styles['sidebar-header']}>
          <div className={styles.logo}>Journalite</div>
          {/* Desktop collapse button */}
          {windowWidth >= 768 ? (
            <button 
              className={styles['toggle-button']} 
              onClick={toggleSidebar}
              aria-label={isSidebarCollapsed ? "Expand menu" : "Collapse menu"}
            >
              {isSidebarCollapsed ? '‚ò∞' : '‚Üê'}
            </button>
          ) : (
            /* Mobile close button */
            <button 
              className={styles['toggle-button']} 
              onClick={toggleSidebar}
              aria-label="Close menu"
            >
              ‚úï
            </button>
          )}
        </div>
        
        <nav className={styles['vertical-nav']}>
          {isAuthenticated ? (
            // Navigation for authenticated users
            <>
              <Link
                href="/"
                className={`${styles['nav-link']} ${styles['nav-home']} ${currentPage === 'home' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Home</span>
              </Link>
              <Link
                href="/my-thoughts"
                className={`${styles['nav-link']} ${styles['nav-thoughts']} ${currentPage === 'my-thoughts' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>My Thoughts</span>
              </Link>
              <Link
                href="/create-article"
                className={`${styles['nav-link']} ${styles['nav-create']} ${currentPage === 'create-article' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Create Article</span>
              </Link>
              <Link
                href="/explore"
                className={`${styles['nav-link']} ${styles['nav-explore']} ${currentPage === 'explore' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Explore</span>
              </Link>
              <Link
                href="/my-profile"
                className={`${styles['nav-link']} ${styles['nav-profile']} ${currentPage === 'my-profile' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Profile</span>
              </Link>
              <button
                onClick={handleSignOut}
                className={`${styles['nav-link']} ${styles['nav-logout']}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Log out</span>
              </button>
            </>
          ) : (
            // Navigation for non-authenticated users
            <>
              <Link
                href="/"
                className={`${styles['nav-link']} ${styles['nav-home']} ${currentPage === 'home' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Home</span>
              </Link>
              <Link
                href="/login"
                className={`${styles['nav-link']} ${styles['nav-login']} ${currentPage === 'login' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Login</span>
              </Link>
              <Link
                href="/learn"
                className={`${styles['nav-link']} ${styles['nav-learn']} ${currentPage === 'learn' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Learn More</span>
              </Link>
              <Link
                href="/plans"
                className={`${styles['nav-link']} ${styles['nav-plans']} ${currentPage === 'plans' ? styles.active : ''}`}
              >
                <span className={styles['nav-icon']}>‚Ä¢</span>
                <span className={styles['nav-text']}>Plans</span>
              </Link>
            </>
          )}
        </nav>
      </aside>
    </>
  );
};

export default SidebarNavigation; 